  <div class="page">
    <Header>
      <template #right>
        <a class="header__button link" href="javascript:void (0);">编辑</a>
      </template>
    </Header>
    <div class="container">
      <h3>
        <a @click="$router.push('/page2')" href="javascript:void (0);">返回上一页</a>
      </h3>
      <p>此页面没有页面权重（meta.index）所以不会有切换效果</p>
      <p>此页未缓存</p>
      <p>此页面需要登录后才允许进入</p>
      <pre>
        meta: {
          title: '第三页',
          needLogin: true,
          keepAlive: false
        },
        components: {
          default: () => import('../views/page3.vue')
        }
    </pre>
    </div>
  </div>
</template>

<script>
import Header from '../components/Header'

export default {
  name: 'test',
  components: {
    Header
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.formPath = from.path
    })
  },
  data() {
    return {
      formPath: null
    }
  },
  methods: {
    callback() {
      if (!this.formPath || this.formPath === '/') {
        this.$router.replace('/')
      } else {
        this.$router.back()
      }
    }
  }
}
</script>

